@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Montserrat', sans-serif;
}

.section1 {
    display: flex;
    flex-direction: row;
    height: 40vw;
}

.left {
    height: 100%;
    width: 55%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.left .outterdiv {
    width: 78%;
    height: 80%;
    margin-left: 9%;
}

.left .outterdiv .first {
    width: 100%;
    height: 14%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.left .outterdiv .first svg {
    width: 40%;
    fill: #fc8019;
}

.left .outterdiv .first .buttons {
    height: 100%;
    width: 36%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.left .outterdiv .first .buttons button {
    height: 65%;
    width: 50%;
    font-size: 1vw;
    font-weight: bold;
    border: none;
}

.left .outterdiv h1 {
    margin-top: 10%;
    font-size: 2.5vw;
    font-weight: bolder;
}

.left .outterdiv h2 {
    font-weight: normal;
    font-size: 1.5vw;
    margin-top: 2%;
    color: #686b78;
}

.left .outterdiv .first .buttons button:first-child {
    background: transparent;
}
.left .outterdiv .first .buttons button:nth-child(2) {
    background: black;
    color: white;
}

.left .outterdiv .second {
    margin-top: 5.5%;
    width: 100%;
    height: 13%;
    display: flex;
    flex-direction: row;
    align-items: center;
}


.left .outterdiv .second input {
    color: lightgray;
    height: 100%;
    width: 100%;
    font-size: 1.1vw;
    padding: 1% 4%;
    border: solid 1px darkgray;
}

.left .outterdiv .second input:focus {
    outline: none;
    border: #fc8019 solid 1px;
}

.left .outterdiv .second input::-webkit-input-placeholder {
    color: dark;
}

.left .outterdiv .second input:-ms-input-placeholder {
    color: dark;
}

.left .outterdiv .second input::placeholder {
    color: dark;
}


.left .outterdiv .second .buttons {
    width: 42.1%;
    height: 100%;
    margin-left: -42%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.left .outterdiv .second .buttons button {
    width: 50%;
    height: 100%;
    border: none;
    font-size: 1vw;
    font-weight: bold;
}

.left .outterdiv .second .buttons button:first-child {
    height: 60%;
    margin: 0 5%;
    width: 40%;
    background: transparent;
    font-weight: lighter;
    font-size: 0.9vw;
}

.left .outterdiv .second .buttons button:first-child:hover {
    background: lightgray;
}

.left .outterdiv .second .buttons button:last-child {
    background: #fc8019;
    color: white;
}

.left .outterdiv p {
    margin-top: 5%;
    font-size: 1vw;
    color: gray;
}

.left .outterdiv p:last-child {
    margin-top: 3%;
    font-size: 1vw;
    font-weight: bold;
    color: gray;
    word-spacing: 0.3vw;
}

.left .outterdiv p:last-child :nth-child(even) {
    color: darkgray;
}

.right {
    width: 45%;
    height: 100%;
    background: url(/static/media/Lunch1_vlksgq.f2f26689.jpeg) center center no-repeat;
    background-size: cover;
}

.section2 {
    height: 28vw;
    width: 100%;
    background: rgb(43, 30, 22);
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.section2 .boxes {
    height: 80%;
    width: 18%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.section2 .boxes img {
    height: 64%;
}

.section2 .boxes h3 {
    color: white;
    margin-top: 18%;
    font-size: 1.2vw;
}

.section2 .boxes p {
    color: #e0cbc1;
    margin-top: 5%;
    font-size: 0.9vw;
    text-align: center;
    line-height: 1.3vw;
}
.section3 {
    height: 40vw;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.section3 .outterdiv {
    height: 100%;
    width: 100%;
    padding: 10.5% 8%;
}

.section3 .outterdiv h1 {
    font-size: 2.4vw;
}

.section3 .outterdiv p {
    margin-top: 5.5%;
    font-size: 1.2vw;
    color: gray;
    line-height: 1.5vw;
}

.section3 .outterdiv .buttons {
    display: flex;
    width: 100%;
    margin-top: 15%;
}

.section3 .outterdiv .buttons a img {
    width: 90%;
    height: 95%;
}

.section3 img {
    height: 85%;
}

.section3 img:nth-child(2) {
    margin-right: -2.5%;
}

.section3 img:last-child {
    align-self: flex-end;
    margin-right: 5%;
}
